        var targetName = e.currentTarget.dataset.contact,
            targetBlock = document.getElementById(targetName),
            targetContact = targetBlock.nextElementSibling;

        scrollToY(targetContact.getBoundingClientRect().top+window.pageYOffset, 1000, 'easeInOutQuint');
