    movePocket: debounce(function(index){
        var _this = this;

        // Returns all pockets to their places,
        // Removing temp pockets
        // Timeout for async
        setTimeout(function () {
            _this.pocketsBack();
        }, 0);

        setTimeout(function () {
            var list = document.getElementsByClassName('move-it'),
                moveIt = list[index],
                landingArea = document.getElementById('landingArea'),
                startCoords = moveIt.getBoundingClientRect(),
                landingCoords = landingArea.getBoundingClientRect(),
                shiftX,
                shiftY;

                shiftX = landingCoords.left - startCoords.left;
                shiftY = landingCoords.top - startCoords.top;

                moveIt.style.transform = 'scale(1,1) translate3d(' + shiftX + 'px,' + shiftY + 'px,0)';
                new TiltFx(moveIt, this.tiltSet);

                //Show temp item
                setTimeout(function () {
                    moveIt.nextElementSibling.classList.add('show');
                }, 400)
        }, 400);

    }, 500),

    pocketsBack: function () {
        var list = document.getElementsByClassName('move-it'),
            temp = document.getElementsByClassName('temp-item');
        for(var i = 0; i < list.length; i++) {
            list[i].style.transform = '';
        }
        for(var j = 0; j < temp.length; j++) {
            temp[j].classList.remove('show');
        }
    },

    scrollHandler: function(e){
        var home = document.getElementById('home'),
            header = document.getElementById('header'),
            homeHalfH = home.offsetHeight*0.5,
            el = home.getElementsByClassName('site-width')[0],
            cluster_1 = el.getElementsByClassName('cluster_11')[0],
            cluster_2 = el.getElementsByClassName('cluster_5')[0],
            _this = this;


        if (window.pageYOffset > 0) {
            this.checkActiveBlock(window.pageYOffset);

            if( !header.classList.contains('not-home') ) {
                header.classList.add('not-home');
                if (document.documentElement.clientWidth > 980) {
                    this.hideHeaderOnScroll();
                }
            }
        } else {
            if( header.classList.contains('not-home') ) {
                header.classList.remove('not-home');
                if (document.documentElement.clientWidth > 980) {
                    this.showHeaderOnScroll();
                }
            }
        }

        if (window.pageYOffset > homeHalfH) {
            el.style.transform = 'translate3d(0,60px,0)';
            cluster_1.style.transform = 'translate3d(0,-90px,0)';
            cluster_2.style.transform = 'translate3d(0,90px,0)';
            return false;
        }

        var shift = 60 * window.pageYOffset / homeHalfH;
        requestAnimationFrame(function () {
            el.style.transform = 'translate3d(0,' + shift + 'px,0)';
            cluster_1.style.transform = 'translate3d(0,' + -shift*1.5 + 'px,0)';
            cluster_2.style.transform = 'translate3d(0,' + shift*1.5 + 'px,0)';
        });

    },

    checkActiveBlock: debounce(function (Y) {
        var blocks = document.getElementsByTagName('section'),
            activeBlock = blocks[0];

        for (var i = blocks.length-1; i >= 0; i--) {
            if ( blocks[i].getBoundingClientRect().top < document.documentElement.clientHeight/2 ) {
                activeBlock = i;
                break;
            }
        }

        this.activeNav = activeBlock;

    }, 100),

    moveBg: function(e){
        var	halfWindowH = document.documentElement.clientHeight*0.5,
            halfWindowW = document.documentElement.clientWidth*0.5,
            //define a max rotation value (X and Y axises)
            maxRotationY = 5,
            maxRotationX = 3,
            aspectRatio;

        var rotateY = ((-event.pageX+halfWindowW)/halfWindowW)*maxRotationY,
            yPosition = event.pageY,
            rotateX = ((yPosition-halfWindowH)/halfWindowH)*maxRotationX;

        if( rotateY > maxRotationY) rotateY = maxRotationY;
        if( rotateY < -maxRotationY ) rotateY = -maxRotationY;
        if( rotateX > maxRotationX) rotateX = maxRotationX;
        if( rotateX < -maxRotationX ) rotateX = -maxRotationX;

        // anime({
        //     targets: document.getElementById('homeCover'),
        //     rotateX: rotateX,
        //     rotateY: rotateY,
        //     translateZ: 0
        // })
        var homeCover = document.getElementById('homeCover'),
            css = 'rotateX(' + rotateX + 'deg' + ') rotateY(' + rotateY + 'deg' + ') translateZ(0)';

        requestAnimationFrame(function () {
            homeCover.style.MozTransform = css;
            homeCover.style.WebkitTransform = css;
            homeCover.style.MsTransform = css;
            homeCover.style.OTransform = css;
            homeCover.style.transform = css;
        });
    }

